<template>
  <div class="container">
    <h1>Latest Posts</h1>
    <div class="create-post">
      <input
        type="text"
        id="create-post"
        v-model="text"
        placeholder="Say something..."
        class="post-input"
      />
      <button v-on:click="createPost" class="post-button">Post!</button>
    </div>
    <hr />
    <p class="error" v-if="error">{{ error }}</p>
    <div class="posts-container">
      <div class="post" v-for="post in posts" :key="post._id">
        <button class="delete-button" @click="deletePost(post._id)">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="24"
            height="24"
            viewBox="0 0 24 24"
          >
            <path d="M0 0h24v24H0z" fill="none" />
            <path
              d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z"
            />
          </svg>
        </button>
        <div class="created-at">
          {{
            `${post.createdAt.getDate()}/${
              post.createdAt.getMonth() + 1
            }/${post.createdAt.getFullYear()}`
          }}
        </div>
        <p class="text">{{ post.text }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from "../PostService";
export default {
  name: "PostComponent",
  data() {
    return {
      posts: [],
      error: "",
      text: "",
    };
  },
  async created() {
    this.fetchPosts();
  },
  methods: {
    async fetchPosts() {
      try {
        this.posts = await PostService.getPosts();
        this.error = ""; // Clear any previous errors
      } catch (err) {
        this.error = "Failed to load posts";
      }
    },
    async createPost() {
      if (!this.text.trim()) {
        this.error = "Post content cannot be empty."; // Client-side validation
        return;
      }
      try {
        await PostService.insertPost(this.text);
        this.text = ""; // Clear the text input upon successful post
        this.fetchPosts();
        this.error = ""; // Clear any previous errors
      } catch (err) {
        this.error =
          err.response && err.response.data
            ? err.response.data
            : "Error creating post"; // Display backend error message
      }
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.fetchPosts();
    },
  },
};
</script>

