import axios from "axios";

const url = "/api/posts/";

class PostService {
  static getPosts() {
    return axios
      .get(url)
      .then((res) =>
        res.data.map((post) => ({
          ...post,
          createdAt: new Date(post.createdAt),
        }))
      )
      .catch((err) => {
        console.error("Error fetching posts:", err.response || err.message);
        throw err;
      });
  }

  static insertPost(text) {
    return axios
      .post(url, {
        text,
      })
      .catch((err) => {
        console.error("Error inserting post:", err.response || err.message);
        throw err;
      });
  }

  static deletePost(id) {
    return axios
      .delete(`${url}${id}`) // Template literal with backticks used here
      .then((response) => {
        console.log("Post deleted successfully:", response.data);
        return response.data;
      })
      .catch((err) => {
        // Log more detailed information about the error
        console.error("Error deleting post: ", err.message);
        console.error("Status code: ", err.response && err.response.status);
        console.error("Error data: ", err.response && err.response.data);
        throw err; // Re-throw the error to ensure that it can be handled further up the chain if necessary
      });
  }
}

export default PostService;
